<template>
  <div>
    <v-navigation-drawer v-model="drawer" absolute app stateless>
      <v-list class="pa-1">
        <v-list-tile avatar>
          <v-list-tile-avatar>
    <v-avatar color="red">
      <span class="white--text headline">{{$store.state.user.username.substring(1, 0)}}</span>
    </v-avatar>
          </v-list-tile-avatar>

          <v-list-tile-content>
            <v-list-tile-title>{{$store.state.user.username}}</v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
      </v-list>

      <v-list class="pt-0" dense>
        <v-divider></v-divider>shop

        <v-list-tile
          v-for="item in items"
          :key="item.title"
          v-bind:to="item.to"
          @click="drawer=!drawer"
        >
          <v-list-tile-action>
            <v-icon>{{ item.icon }}</v-icon>
          </v-list-tile-action>

          <v-list-tile-content>
            <v-list-tile-title>{{ item.title }}</v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
        <v-list-tile @click="password">
          <v-list-tile-action>
            <v-icon>lock</v-icon>
          </v-list-tile-action>

          <v-list-tile-content>
            <v-list-tile-title>Change Password</v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>

        <v-list-tile @click="logout">
          <v-list-tile-action>
            <v-icon>logout</v-icon>
          </v-list-tile-action>

          <v-list-tile-content>
            <v-list-tile-title>Logout</v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
      </v-list>
    </v-navigation-drawer>

    <v-toolbar color="teal " dark app flat>
      <v-toolbar-side-icon @click="drawer=!drawer"></v-toolbar-side-icon>

      <v-toolbar-title>
        Sure
        <b>Chat</b>
      </v-toolbar-title>

      <v-spacer></v-spacer>

      <v-btn icon to="/">
        <v-icon>home</v-icon>
      </v-btn>
      <v-menu
        bottom
        left
        offset-y
        :close-on-content-click="false"
        :min-width="300"
        :max-width="500"
        transition="slide-x-reverse-transition"
      >

      </v-menu>
    </v-toolbar>
  </div>
</template>

<script>
import axios from "axios";
import Cookies from "js-cookie";

export default {
  data() {
    return {
      
      drawer: false,
      items: []
    };
  },
  methods: {
    getOrders: function() {
      this.$store.commit("GET_CART_UPDATE");
    },
    checkout: function() {
      let config = {
        headers: {
          "X-CSRFToken": Cookies.get("csrftoken")
        }
      };
      axios
        .post("api/store/order/checkout/", {}, config)
        .then(response => {
          this.$store.commit("GET_CART_UPDATE");
        })
        .catch(error => {
          console.log(error);
        });
    },
    getUserList: function() {
      let id;
      let user;
      let endpoint = "api/accounts/me/";
      axios
        .get(endpoint)
        .then(response => {
          id = response.data.id;
          user = response.data;
          
            this.items = [
              { title: "Messages", icon: "chat", to: "/chat" }
            ];
              
            
          
          this.items.push();
        })
        .catch(error => {
          console.log(error);
        });
    },
    logout: function() {
      window.location.replace(
        window.location.protocol +
          "//" +
          window.location.host +
          "/accounts/logout/"
      );
    },
    password: function() {
      window.location.replace(
        window.location.protocol +
          "//" +
          window.location.host +
          "/accounts/password/"
      );
    }
  },
  mounted() {
    
    this.getUserList();
  }
};
</script>

