<template>
  <div class="row">
    <div class="chat-body">
      <v-container grid-list-md text-xs-center>
        <v-flex>
          <div v-for="(item, index) in threads" :key="index">
            <v-avatar v-if="index == current_thread_index" color="orange" size="50">
              <b
                style="color:white;font-size:200%;cursor:pointer;"
                v-if="$store.state.user.id == item.chat.id"
                v-bind:title="item.doctor.username"
                @click="setCurrentThread(index)"
              >{{item.doctor.username.charAt(0).toUpperCase()}}</b>
              <b
                style="color:white;font-size:200%;cursor:pointer;"
                v-else
                @click="setCurrentThread(index)"
                v-bind:title="item.chat.username"
              >{{item.chat.username.charAt(0).toUpperCase()}}</b>
            </v-avatar>

            <v-avatar v-else color="teal" size="50">
              <b
                style="color:white;font-size:200%;cursor:pointer;"
                v-if="$store.state.user.id == item.chat.id"
                v-bind:title="item.doctor.username"
                @click="setCurrentThread(index)"
              >{{item.doctor.username.charAt(0).toUpperCase()}}</b>
              <b
                style="color:white;font-size:200%;cursor:pointer;"
                v-else
                @click="setCurrentThread(index)"
                v-bind:title="item.chat.username"
              >{{item.chat.username.charAt(0).toUpperCase()}}</b>
            </v-avatar>
            <hr>
          </div>
        </v-flex>
      </v-container>
    </div>
    <link
      rel="stylesheet"
      href="https://stackpath.bootstrapcdn.com/bootstrap/4.3.1/css/bootstrap.min.css"
    >

    <div class="col msg-window-container">
      <div class="card" id="msgWindow">
        <div class="card-header">
          <span
            v-if="current_thread_index !== null&& $store.state.user.is_doctor && threads[current_thread_index].can_view_data"
            class="card-title"
          >
            {{current_chat}}
            <v-btn color="success" @click=goToRecords>chat Records</v-btn>
          </span>
          <span
            v-if="current_thread_index !== null && $store.state.user.is_doctor && !threads[current_thread_index].can_view_data"
            class="card-title"
          >{{current_chat}}</span>
          <span v-if="!$store.state.user.is_doctor" class="card-title">
            {{current_chat}}
            <v-switch
              v-if="current_thread"
              label="Can View Files"
              v-model="threads[current_thread_index].can_view_data"
              @change="updateThreadPermission"
            ></v-switch>
          </span>
        </div>
        <div v-if="current_thread_index !== null" class="msg annoucement">Chat started!</div>
        <div class="card-body" id="msgs" v-for="(message, index) in messages" :key="index">
          <slot>
            <div
              v-if="message.sender.id === $store.state.user.id"
              class="msg from"
              v-bind:title="message.timestamp"
            >{{message.text}}</div>
            <div v-else class="msg to" v-bind:title="message.timestamp">{{message.text}}</div>
          </slot>
        </div>
      </div>

      <div class="card-footer">
        <div class="input-group" id="msgForm" data-sender="me">
          <input
            class="form-control"
            type="text"
            v-model="new_message"
            placeholder="Type message and hit [Enter] to send."
            @keydown.enter="sendMessage"
          >
          <div class="input-group-append">
            <button class="btn btn-outline-secondary" type="button" @click="sendMessage">Send</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script src="https://code.jquery.com/jquery-3.1.1.slim.min.js" integrity="sha384-A7FZj7v+d/sdmMqp/nOQwliLvUsJfDHW+k9Omg/a/EheAdgtzNs3hpfag6Ed950n" crossorigin="anonymous"></script>
<!-- Latest compiled and minified JavaScript -->
<script src="https://maxcdn.bootstrapcdn.com/bootstrap/3.3.7/js/bootstrap.min.js" integrity="sha384-Tc5IQib027qvyjSMfHjOMaLkfuWVxZxUPnCJA7l2mCWNIpG9mGCD8wGNIcPD7Txa" crossorigin="anonymous"></script>
<script>
ObjDiv = document.getElementById("msgWindow");
ObjDiv.scrollTop = ObjDiv.scrollHeight;
</script>

<script>
import axios from "axios";
import Cookies from "js-cookie";
export default {
  data() {
    return {
      messages: [],
      threads: [],
      current_thread: null,
      current_thread_index: null,
      current_chat: null,
      new_message: null
    };
  },
  mounted() {
    var chat = document.getElementById("msgWindow")[0];
    chat.scrollTop = chat.scrollHeight;
  },

  methods: {
    getThreads: function() {
      let endpoint = "api/chat/threads/";
      axios
        .get(endpoint)
        .then(response => {
          this.threads = response.data;
        })
        .catch(error => {
          console.log(error);
        });
    },
    setCurrentThread: function(index) {
      this.current_thread_index = index;
      this.current_thread = this.threads[index].id;
      if (this.$store.state.user.id == this.threads[index].doctor.id) {
        this.current_chat = this.threads[index].chat.username;
      } else {
        this.current_chat = this.threads[index].doctor.username;
      }
      axios
        .get(`api/chat/messages/${this.current_thread}/`)
        .then(response => {
          this.messages = response.data;
          let config = {
            headers: {
              "X-CSRFToken": Cookies.get("csrftoken")
            }
          };
          axios
            .put(
              `api/chat/messages/${this.current_thread}/read/`,
              { read: true },
              config
            )
            .then(response => {})
            .catch(error => {
              console.log(error);
            });
        })
        .catch(error => {
          console.log(error);
        });
    },
    sendMessage: function() {
      if (this.current_thread_index !== null) {
        let endpoint = `api/chat/messages/create/`;
        let message = { text: this.new_message, thread: this.current_thread };
        let config = {
          headers: {
            "X-CSRFToken": Cookies.get("csrftoken")
          }
        };
        axios
          .post(endpoint, message, config)
          .then(response => {
            this.new_message = null;
            this.setCurrentThread(this.current_thread_index);
          })
          .catch(error => {
            console.log(error);
          });
      }
    },
    update: function() {
      setInterval(
        function() {
          if (this.current_thread_index !== null) {
            this.setCurrentThread(this.current_thread_index);
          }
        }.bind(this),
        1000
      );
    },
    updateThreadPermission: function() {
      let endpoint = `api/chat/threads/${this.current_thread}/toggle/`;
      let data = {
        can_view_data: this.threads[this.current_thread_index].can_view_data
      };
      let config = {
        headers: {
          "X-CSRFToken": Cookies.get("csrftoken")
        }
      };
      axios
        .put(endpoint, data, config)
        .then(response => {
          this.threads[this.current_thread_index].can_view_data =
            response.data.can_view_data;
          console.log(response.data);
        })
        .catch(error => {
          console.log(error);
        });
    },
    goToRecords : function () {
      this.$router.push({ path: `/records/${this.threads[current_thread_index].chat.id}`})
    }
  },
  mounted() {
    this.getThreads();
    this.update();
  }
};
</script>
<style sccoped>
